.statusPage {
  max-width: 600px;
  margin: 2rem auto;
  padding: 0 1rem;
  display: grid;
  gap: 1rem;
}

.statusCard {
  padding: 1rem;
  border: 1px solid #ccc;
  border-radius: 8px;
  background: #fafafa;
  word-wrap: break-word;
}

.statusCard p {
  margin: 0;
}

.statusCard small {
  display: block;
  margin-top: 0.5rem;
  font-size: 0.75rem;
  color: #666;
}
